html {
  font-family: Arial, Helvetica, sans-serif;
  font-size: 100px;
}

* {
  box-sizing: border-box;
}

body {
  margin: 0;
}

.container {
  display: flex;
  flex-wrap: wrap;
}

.block {
  height: 300px;
  min-width: 300px;
  width: 100%;

  color: white;

  flex-grow: 1;
  display: flex;
  align-items: center;
  justify-content: center;
}

.block--1 {
  background-color: red;
}

.block--2 {
  background-color: rgb(204 0 0);
}

.block--3 {
  background-color: rgb(153 0 0);
}

.block--4 {
  background-color: rgb(102 0 0);
}

.block--5 {
  background-color: rgb(51 0 0);
}

.block--6 {
  background-color: rgb(0 0 0);
}

@media (min-width: 600px) {
  .block {
    width: 50%;
  }

  .block--3 {
    order: 1;
  }

  .block--5 {
    order: 1;
  }

  .block--6 {
    order: 1;
  }
}

@media (min-width: 900px) {
  .block {
    width: 33%;
  }

  .block--6 {
    order: 1;
  }

  .block--5 {
    order: 2;
  }

  .block--4 {
    order: 3;
  }
}
